@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
    margin: 0;
    background-color: #1e1e1e;
    background-color: var(--background-color);
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
 
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
    --primary-color: #fd5056;
    --primary-color-dark: #f7343a;
    --secondary-color: #f3f4f5;
    --secondary-color-dark: #11134f;
    --background-color: #1e1e1e;
    --background-color-dark: #131313;
    --primary-text-color: #f3f4f5;
    --secondary-text-color: #fd5056;
    --color-black: #000000;
    --border-bottom-white: #dfdfdf;
    --playlist-text-overlay: rgba(8, 8, 8, 0.779);
}
.App{
  color: var(--primary-text-color);
  background-color: var(--background-color);
  width: 100%;
  min-height: 100vh;
  max-width: 1800px;
  margin: auto;
}
.inline{
  display: inline;
}
.inline-block{
  display: inline-block;
}
.content-section{
  display: grid;
  grid-template-columns: 250px auto;
  min-height: -webkit-max-content;
  min-height: max-content;
}
.footer{
    display: none;
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 60px;
    background-color: var(--background-color);
}
.footer-items{
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
.footer-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.footer-item-icon{
    font-size: 1.8rem;
}
.footer-item-active{
    color: var(--primary-color);
    font-weight: 600;
}
.footer-item-text{
    margin-top: 3px;
}
@media only screen and (max-width: 800px){
    .footer{
        display: block;
    }
}
.navbar{
    font-size: 1.5rem;
}
.logo{
    margin: 0 2rem 0rem 1rem;
}
.logo-sub-text{
    font-size: inherit;
    color: var(--secondary-text-color);
}
.searchbar-container{
    flex: 2 1;
}
.btn-container-navbar{
    margin: 0 1rem;
}
/* Due to specificity issues i have to write this */
.input-box .navbar-search{
    width: 100%;
}
.navbar-search{
    color: var(--primary-text-color);
}
.input-box .navbar-search:focus{
    border: 2px solid var(--primary-text-color);
}
@media only screen and (max-width: 600px){
    .logo{
        margin: 0 1rem;
    }
}
.button-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2px;
    font-size: 1.3rem;
}
.button-primary{
    background-color: var(--primary-color-dark);
    color: var(--primary-text-color);
}
.button-primary:hover{
    background-color: var(--primary-color);
    border: 2px solid var(--primary-color-dark);
}
.button-outline-secondary{
    background-color: transparent;
    border: 2px solid var(--primary-color);
}
.button-outline-secondary:hover{
    background-color: var(--primary-color);
}

.waviy {
    position: relative;
    -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0,0,0,.2));
    font-size: 2rem;
    position: absolute;
    top: 50%; 
    right: 50%;
    -webkit-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
  }
  .waviy span {
    position: relative;
    display: inline-block;
    color: var(--primary-text-color);
    -webkit-animation: waviy 1s infinite;
            animation: waviy 1s infinite;
    -webkit-animation-delay: calc(.1s * var(--i));
            animation-delay: calc(.1s * var(--i)); 
    font-weight: 600; 
    font-size: 3rem;
  }
  @-webkit-keyframes waviy {
    0%,40%,100% {
      -webkit-transform: translateY(0);
              transform: translateY(0)
    }
    20% {
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px)
    }
  }
  @keyframes waviy {
    0%,40%,100% {
      -webkit-transform: translateY(0);
              transform: translateY(0)
    }
    20% {
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px)
    }
  }
