    margin: 0.5rem;
    border-radius: 0.1rem;
    cursor: pointer;
    background-color: var(--background-color-dark);
}
.videolisting-video-thumbnail{
    object-fit: contain;
}
.videolisting-thumbnail-container{
    position: relative;
    width: 100%;
    height: 15rem;
}
.videolisting-thumbnail-overlay-container{
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: none;
  
}
