      display: flex;
    justify-content: center;
    align-items: center;
}
.thumbnail-text-container{
    display: grid;
    grid-template-columns: 3.1rem auto;
    margin: 1rem 0.5rem;
}
.thumbnail-container{
    grid-column-start: 1;
    grid-column-end: 2;
